import React, { Component } from "react";
import styled from "styled-components";
import DetailTabHeader from "./DetailTabHeader";
import TabYoutube from "./TabYoutube";
import TabCompany from "./TabCompany";
import TabCountry from "./TabCountry";
import TabSeason from "./TabSeason";
import TabCasts from "./TabCasts";
const Container = styled.div`
  display: block;
  width: 80%;
  background-color: rgba(30, 30, 30, 0.8);
  height: 100%;
  max-height: 380px;
  margin-bottom: 40px;
`;

export default class DetailTabContainer extends Component {
  state = {
    selectedButton: null,
    seasons: null,
    results: null,
    casts_result: null,
    production_companies: null,
    production_countries: null,
  };

  componentDidUpdate() {
    console.log("Did Update in Tab");
  }
  componentDidMount() {
    console.log("Did Mount in Tab");
    let selectedButton = null;
    const {
      result: {
        videos: { results },
        production_companies,
        production_countries,
      },
      casts_result,
      isMovie,
    } = this.props;
    let seasons = null;
    if (!isMovie) {
      ({
        result: { seasons: seasons },
      } = this.props);
    }

    if (results && results.length > 0) {
      selectedButton = "Youtube";
    } else if (casts_result && casts_result.length > 0) {
      selectedButton = "Casts";
    }
    //  else if (production_companies && production_companies.length > 0) {
    //   selectedButton = "Company";
    // }
    else if (production_countries && production_countries.length > 0) {
      selectedButton = "Country";
    } else if (seasons && seasons.length > 0) {
      selectedButton = "Seasons";
    }
    this.setState({
      selectedButton,
      seasons,
      results,
      production_companies,
      production_countries,
    });
  }

  handleClick = (selectedButton) => {
    this.setState({
      selectedButton,
    });
  };

  render() {
    const {
      selectedButton,
      seasons,
      results,
      production_companies,
      production_countries,
    } = this.state;
    const { casts_result } = this.props;
    const isVideos = results && results.length > 0;
    const isCasts = casts_result && casts_result.length > 0;
    // const isCompanies = production_companies && production_companies.length > 0;
    const isCountries = production_countries && production_countries.length > 0;
    const isSeasons = seasons && seasons.length > 0;
    console.log("render -- 1 -- ");
    /* If selectedButton is null, wait til next rendering  */
    return selectedButton ? (
      <Container>
        <DetailTabHeader
          isVideos={isVideos}
          isCasts={isCasts}
          // isCompanies={isCompanies}
          isCountries={isCountries}
          isSeasons={isSeasons}
          handleClick={this.handleClick}
          selectedButton={selectedButton}
        />
        {console.log("render -- 2 -- ")}
        {selectedButton === "Youtube" && isVideos && results.length > 0 && (
          <TabYoutube youtubeKey={results[0].key} />
        )}
        {selectedButton === "Casts" && casts_result.length > 0 && (
          <TabCasts casts_result={casts_result} />
        )}
        {/* {selectedButton === "Company" && production_companies.length > 0 && (
          <TabCompany companies={production_companies} />
        )} */}
        {selectedButton === "Country" && production_countries.length > 0 && (
          <TabCountry countries={production_countries} />
        )}
        {selectedButton === "Seasons" && seasons.length > 0 && (
          <TabSeason seasons={seasons} />
        )}
      </Container>
    ) : null;
  }
}
