import React from "react";
import styled from "styled-components";
import ModalPortal from "../ModalPortal";
import CastModal from "../CastModal";

const BigContainer = styled.div`
  position: relative;
  margin-right: 40px;
  text-align: center;
  margin-top: 30px;
`;
const Container = styled.div`
  position: relative;
  height: 290px;
  max-width: 160px;
  margin: 0 auto;

  &::before {
    content: "";
    display: block;
    width: 100%;
    /* height: 175px; */
    outline: 5px solid #72a9e3;
    position: absolute;
    /* top: 56px; */
    left: 6px;
    /* max-width: 150px; */
    width: 200px;
    height: 216px;
    top: 24px;
  }
`;

const SliderWrapper = styled.div`
  height: 100%;
  /* width:100%; */
  display: flex;
  align-items: center;
  position: absolute;
  transition: transform 500ms cubic-bezier(0.413, 0.03, 0.1, 0.4);
`;

const CastContainer = styled.div`
  ${(props) =>
    props.index === props.nowTarget
      ? "opacity:1;transform: scale(1);"
      : Math.abs(props.nowTarget - props.index) > 1
      ? "opacity:0;"
      : "opacity:0.5; transform: scale(0.7);"}
  transition: all 0.3s linear;
`;

const Cover = styled.img`
  /* width: 150px;
  height: 180px; */
  width: 200px;
  height: 220px;
`;
const CastName = styled.span`
  display: block;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  margin-top: 8px;
`;
const PrevButton = styled.button`
  font-size: 20px;
  position: absolute;
  left: 0;
  top: -22px;
  z-index: 1;

  &:disabled {
    color: grey;
  }
`;

const NextButton = styled.button`
  font-size: 20px;
  position: absolute;
  right: -36px;
  top: -22px;
  z-index: 1;
  &:disabled {
    color: grey;
  }
`;

const CastButton = styled.button``;

export default class TabCasts extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      index: 0,
      modalOpen: false,
      cast_id: null,
    };
    this.nextButton = this.nextButton.bind(this);
    this.preButton = this.preButton.bind(this);
    this.handleModalOpen = this.handleModalOpen.bind(this);
    this.handleModalClose = this.handleModalClose.bind(this);
  }
  nextButton = () => {
    let newIndex = this.state.index + 1;
    this.setState({ index: newIndex });
  };
  preButton = () => {
    let newIndex = this.state.index - 1;
    this.setState({ index: newIndex });
  };
  handleModalOpen = (cast_id) => {
    this.setState({ modalOpen: true, cast_id: cast_id });
  };
  handleModalClose = () => {
    this.setState({ modalOpen: false, cast_id: null });
  };
  render() {
    const { index, modalOpen } = this.state;
    const { casts_result } = this.props;
    console.log(`index : ${this.state.index}`);
    console.log(`cast_result.length : ${casts_result.length}`);
    return (
      <BigContainer>
        {" "}
        <PrevButton onClick={() => this.preButton()} disabled={index === 0}>
          Prev
        </PrevButton>
        <NextButton
          onClick={() => this.nextButton()}
          disabled={index === casts_result.length - 1}
        >
          Next
        </NextButton>
        <Container>
          <SliderWrapper
            style={{
              transform: `translateX(-${
                this.state.index * (100 / casts_result.length)
              }%`,
            }}
          >
            {casts_result &&
              casts_result.map((cast, idx) => (
                <CastContainer index={idx} nowTarget={index}>
                  <CastButton
                    onClick={() => this.handleModalOpen(cast.id)}
                    disabled={idx !== index}
                  >
                    <Cover
                      src={
                        cast.profile_path
                          ? `https://image.tmdb.org/t/p/w200${cast.profile_path}`
                          : require("../../assets/noProfile.png")
                      }
                    ></Cover>

                    <CastName>
                      {cast.name.length < 20
                        ? cast.name
                        : `${cast.name.substring(0, 18)}..`}
                    </CastName>
                  </CastButton>
                </CastContainer>
              ))}
          </SliderWrapper>
          {modalOpen && (
            <ModalPortal>
              <CastModal
                cast_id={this.state.cast_id}
                onClose={this.handleModalClose}
              />
            </ModalPortal>
          )}
        </Container>
      </BigContainer>
    );
  }
}
